import { teamCardData } from "./constant";

const TeamCard = (props) => {
  return (
    <div className="team-card-wrapper">
      {teamCardData?.map((data, index) => {
        return (
          <div className="card-container" key={index}>
            <img src={data?.image} className="card-img" />
            <span className="bold-text">{data?.name}</span>
            <span className="light-text">{data?.role1}</span>
            <span className="light-text">{data?.role2}</span>
          </div>
        );
      })}
    </div>
  );
};

export default TeamCard;
